body {
    margin: 0;
    overflow-x: hidden;
}

.footer {
    background-color: whitesmoke;
    padding: 30px 0px;
    font-family: 'Play', sans-serif;
    text-align: center;
    position: relative;
}

.footer .row {
    width: 100%;
    margin: 1% 0%;
    padding: 0.6% 0%;
    color: gray;
    font-size: 0.8em;
}

.footer .row a {
    text-decoration: none;
    color: gray;
    transition: 0.5s;
}

.footer .row a:hover {
    color: palegoldenrod;
}

.footer .row ul {
    width: 100%;
}

.footer .row ul li {
    display: inline-block;
    margin: 0px 30px;
}

.footer .row a i {
    font-size: 2em;
    margin: 0% 1%;
}